/*
  Based on the Eric Meyer CSS Reset v2.0 (http://meyerweb.com/eric/tools/css/reset/)
*/
@import url(//fonts.googleapis.com/earlyaccess/droidarabickufi.css);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: top; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  color: #364A4D;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: 100%; }

h1,
h2,
h3,
h4,
h5,
h6 {
  text-transform: uppercase;
  color: #E86D15;
  font-weight: 700; }

h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-size: 1.5rem; }

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-size: 1.5rem; }

h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-size: 1.25rem; }

h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  font-size: 1.125rem; }

h5 {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-size: 1rem;
  font-size: 10px;
  color: #98B2B7; }

h6 {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-size: 1rem; }

p {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-size: 1rem;
  margin: 1em 0;
  line-height: 1.2; }

a {
  color: #E86D15;
  text-decoration: none; }

abbr,
acronym {
  border-bottom: 1px dotted;
  cursor: help; }

address {
  font-size: 1rem;
  font-style: italic; }

blockquote {
  margin: 1em 0;
  padding: 0 0 0 2em; }

cite {
  font-style: italic; }

code {
  font-size: 0.96rem;
  font-family: monospace;
  background: #EEEEEE;
  border: 1px solid #e1e1e1;
  padding: 0.125em 0.25em;
  line-height: 150%; }

em,
i {
  font-style: italic; }

strong,
b {
  font-weight: bold; }

hr {
  outline: none;
  border: none;
  margin: 1em 0;
  padding: 0;
  border-top: 1px solid #EEEEEE; }

ol,
ul {
  margin: 1em 0;
  padding: 0 0 0 2em; }
  ol li,
  ul li {
    font-size: 1rem;
    list-style-position: outside; }

ul li {
  list-style: disc; }

ol li {
  list-style: decimal; }

form {
  margin: 1em 0; }

dl {
  margin: 1em 0; }
  dl dt {
    font-weight: bold; }
  dl dd {
    margin: 0 0 0.5em; }

table {
  width: 100%;
  margin: 1em 0; }
  table th {
    padding: 0.5em;
    border: 1px solid #EEEEEE;
    background: #f7f7f7;
    text-align: left;
    font-weight: bold; }
  table td {
    padding: 0.5em;
    border: 1px solid #EEEEEE; }

* {
  box-sizing: border-box; }

.container {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto; }
  .container:after {
    content: "";
    display: table;
    clear: both; }
  @media (min-width: 1200px) {
    .container {
      max-width: 1200px; } }
  @media (max-width: 1024px) {
    .container {
      max-width: 100%; } }
  @media (max-width: 736px) {
    .container {
      max-width: 100%; } }

.error {
  color: #AA0000; }

.success {
  color: #00AA00; }

html {
  background-color: #F4F4F4; }

main {
  padding: 48px 0;
  margin-top: 4px;
  background-color: white; }
  main:after {
    content: "";
    display: table;
    clear: both; }

